.App{
  width: 100%;
  margin-top: 8rem;

}

.Home {
  text-align: center;
  width: 80%;
  margin:auto;
}
.Title-Wrapper{
  /* border-color: white;
  border-style: solid; */
  background-color: #B388EBCC;
  -webkit-clip-path: polygon(
    0 8%,
    6% 0,
    94% 0,
    100% 8%,
    100% 92%,
    94% 100%,
    6% 100%,
    0% 92%,
    0% 8%
  );
          clip-path: polygon(
    0 8%,
    6% 0,
    94% 0,
    100% 8%,
    100% 92%,
    94% 100%,
    6% 100%,
    0% 92%,
    0% 8%
  )
}

.subpage{
  text-align: center;
  width: 80%;
  max-width: 1000px;
  align-items: center;
  display: flex;
  margin: auto;
  flex-direction: column; 
  justify-content: center;
  font-family: "Abel", sans-serif;
  font-size: 1.5rem;
  color: #525252;
 
}

.Back-Button{
  border: solid white .15rem;
  margin-top: 2rem;
  background-color: #B388EBCC;
  font-family: "Abel", sans-serif;
  font-size: 30px;
  font-weight: bold;
  color: #525252;
  margin-bottom: 2rem;
}

.Back-Button:hover{
  color: #8093f1;
}

.App-logo {
  height: 30vmin;
  margin: 0;
  pointer-events: none;
  align-self: center;
}

@-webkit-keyframes logoani {
  0% {
    transform: scale(0.90);
  }
  50% {
    transform: scale(1.15);
  }
  100% {
    transform: scale(0.90);
  }
}

@keyframes logoani {
  0% {
    transform: scale(0.90);
  }
  50% {
    transform: scale(1.15);
  }
  100% {
    transform: scale(0.90);
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: logoani infinite 5s;
            animation: logoani infinite 5s;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    margin: 0 0 3rem 0
  }
}

.App-header {
  background-color: rgba(100, 0, 100, 0);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;

}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Tile{
    display: flex;
    flex-direction: row;
    border: solid 0.5rem #525252;
    font-size: 0;
    background: #F4F4EDBB
}

.SWName{
    margin-top: 1.5rem;
    font-size: 2rem;
    font-weight: bold;
    color: #525252;
}
.SWImg{
    border: solid 0.5rem #525252BB;
    border-top-style: none;
    border-right-style: solid;
    border-bottom-style: none;
    border-left-style: none;
}

.SWDescription{
    font-size: 1.5rem;
    margin: 1rem;
}
.SWYear{
    font-size: 1rem;
}
.SWLink,
.SWLink.visted{
    margin-top: 1.5rem;
    font-size: 1.5rem;
    color: #525252;
}
.SWLink:hover{
    color: #8093f1;
}

.List > *{
    margin-top: 2rem;
}


